import React, { ReactNode, ReactChild, useState } from "react"

import { NavPillMakerProps } from "./NavPillMaker.types"
import "./NavPillMaker.css"

const NavPillMaker: React.FC<NavPillMakerProps> = ({
  children,
  activeStyles,
  inactiveStyles,
}) => {
  if (!Array.isArray(children)) {
    return <div>ERROR: NavPillMaker expects an array of children</div>
  }
  const labelIds = children.filter(child => {
    return child && child.props && child.props.label && child.props.label.id
  })
  const labelChildren = labelIds.map(child => {
    return { id: child.props.label.id, child: child }
  })

  console.log(labelChildren, "children")
  const [activeChildId, setActiveChildId] = useState<string>(
    labelChildren[0].id
  )

  return (
    <div data-testid="NavPillMaker" className="NavPillMaker">
      <div className="dummyContainer">
        <ul className="labelContainer">
          {labelIds.map((child, index) => {
            return (
              <li
                key={index}
                onClick={() => {
                  setActiveChildId(child.props.label.id)
                }}
                style={
                  activeChildId === child.props.label.id
                    ? activeStyles
                    : inactiveStyles
                    ? inactiveStyles
                    : {}
                }
              >
                {child.props.label.text}
              </li>
            )
          })}
        </ul>
        <div className="description">
          {labelChildren
            .filter(child => child.id === activeChildId)
            .map(child => child.child)}
        </div>
      </div>
    </div>
  )
}

export default NavPillMaker
