import React from "react"

import { CtaButtonProps } from "./CtaButton"
import "./CtaButton.css"
import { Link } from "react-router-dom"



const CtaButton: React.FC<CtaButtonProps> = ({
  text,
  link,
  type,
  onClick,
}) => {
  const classNames = {
    primary: "PrimaryCtaButton",
    secondary: "SecondaryCtaButton",
  }
  if (!type)
      type="primary"

  if (link && onClick) {
    return <>"ERROR: PrimaryCtaButton can't have both a link and an onClick"</>
  }
  if (link) {
    return (
      <Link to={link}>
        <button
          data-testid="CtaButton"
          className={`ctaButton ${classNames[type]}`}
        >
          {text}
        </button>
      </Link>
    )
  }
  if (onClick) {
    return (
      <button
        data-testid="CtaButton"
        className={`ctaButton ${classNames[type]}`}
        onClick={onClick}
      >
        {text}
      </button>
    )
  }
  return null;
}

export default CtaButton
