import { createSlice, createAsyncThunk, PayloadAction } from "@reduxjs/toolkit"
import { RootState } from "./store";
import { CourseInfo } from "../types";
import { fetchCourseInfo } from "./dummyFetchCourses";


export interface CourseInfoState {
    courses: CourseInfo[],
    requestState: string
}

const initialState: CourseInfoState = {
    courses: [],
    requestState: "idle"
}


export const setCourses = createAsyncThunk(
    "courseInfo/setCourses",
    async () => {
        const response: {courses: CourseInfo[]} = await fetchCourseInfo();
        return response;
    }
);

export const selectCourses = (state: RootState) => state.courses.courses;
export const selectCoursesIds = (state: RootState) => state.courses.courses.map(course => course.id);
export const selectCourse = (state: RootState, id: string) => {
    const filteredCourses = state.courses.courses.filter(course => course.id === id);
    if (filteredCourses.length === 0) {
        return null;
    }
    return filteredCourses[0];
}
export const selectRequestState = (state: RootState) => state.courses.requestState;
export const selectSyllabus = (state: RootState, courseId: string, standard: string ) => {
    const course  = selectCourse(state, courseId);
    if (course === null) {
        return null;
    }
    const filteredSyllabus = course.syllabus_info.filter(syllabus => syllabus.standard === standard);
    if (filteredSyllabus.length === 0) {
        return null;
    }
    return filteredSyllabus[0];
}

const courseInfoSlice = createSlice({
    name: "courseInfo",
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(setCourses.fulfilled, (state, action: PayloadAction<{ courses: CourseInfo[] }>) => {
                state.courses = action.payload.courses;
                state.requestState = "fulfilled";
            })
            .addCase(setCourses.pending, (state) => {
                state.requestState = "pending";
            })
            .addCase(setCourses.rejected, (state) => {
                state.requestState = "rejected";
            })
    },
})

export default courseInfoSlice.reducer;

