import React from "react"

import { LandingPageProps } from "./LandingPage.types"
import HeroSection from "../HeroSection"
import CourseLPCard from "../CourseLPCard"
import { useAppSelector } from "../../redux/hooks"
import { selectCoursesIds } from "../../redux/CourseInfoSlice"
import "./LandingPage.css"

const LandingPage: React.FC<LandingPageProps> = () => {
  const courseIds = useAppSelector(selectCoursesIds)
  return (
    <div data-testid="LandingPage" className="LandingPage">
      <HeroSection />
      <h2 className="courseListingsHeader">Visit our Courses</h2>
      <div className="courseListings">
        {/*put a loop and list courses and pass all data for course in the prop*/}
        {courseIds.map(id => {
          return <CourseLPCard courseId={id} key={id} />
        })}
      </div>
    </div>
  )
}

export default LandingPage
