import { createSlice, createAsyncThunk, PayloadAction } from "@reduxjs/toolkit"
import { RootState } from "./store"
import "./checkout"

export interface PaymentState {
    orderId: string | null;
    requestState: string
}

const initialState: PaymentState = {
    orderId: null,
    requestState: "idle"
}
export interface PaymentOrderPayloadType {
    receiptId: string,
    amount: number
}

export interface PaymentPaymentPayloadType {
    orderId: string,
    amount: number
}

export const selectOrderId = (state: RootState) => state.payment.orderId;

export const setOrderId = createAsyncThunk(
    "payment/setOrderId",
    async (requestPayload: PaymentOrderPayloadType) => {
        const response = await fetch("http://localhost:8000/payments/create_order/", {
            method: "POST",
            body: JSON.stringify({
                receipt: `${requestPayload.receiptId}`,
            }),
            headers: {
                "Content-Type": "application/json",
            },
        })
            .then((response) => response.json())
            .then((responseJson) => {
                console.log("responseJson", responseJson);
                return responseJson
            })
            .then((response) => {
                var options = {
                    key_id: "rzp_test_dummy", // Enter the Key ID generated from the Dashboard
                    amount: requestPayload.amount * 100, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
                    currency: "INR",
                    order_id: response.orderId,
                    name: "Acme Corp", //your business name
                    description: "Test Transaction",
                    image: "https://example.com/your_logo",
                    callback_url: "https://eneqd3r9zrjok.x.pipedream.net/",
                    prefill: {
                        name: "Swapnil Kumar", //your customer's name
                        email: "swapnil.kumar@example.com",
                        contact: "9000090000",
                    },
                    notes: {
                        address: "Razorpay Corporate Office",
                    },
                    theme: {
                        color: "#3399cc",
                    },
                };
                var rzp1 = new Razorpay(options);
                rzp1.open();
                return response
            })
        console.log('ye amount bhejna h', requestPayload.amount, response)
        return response.orderId;
    }
);

const paymentSlice = createSlice({
    name: "payment",
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(setOrderId.fulfilled, (state, action: PayloadAction<string>) => {
                state.orderId = action.payload;
                state.requestState = "OrderGenerated";
            })
            .addCase(setOrderId.pending, (state) => {
                state.requestState = "pending";
            })
            .addCase(setOrderId.rejected, (state) => {
                state.requestState = "rejected";
            })
    },
});



export default paymentSlice.reducer;

