import * as React from "react"

import LandingPage from "../LandingPage"
import ContactUsPage from "../ContactUsPage"
import ProductPage from "../ProductPage"
import { Route } from "react-router-dom"
import { RootProps } from "./Root.types"
import CourseIntroductionPage from "../CourseIntroductionPage"
import FaqPage from "../FaqPage"
import SyllabusPage from "../SyllabusPage"
import { useAppDispatch } from "../../redux/hooks"
import { setCourses } from "../../redux/CourseInfoSlice"
import "./Root.css"

const Root: React.FC<RootProps> = ({}) => {
  const dispatch = useAppDispatch()
  React.useEffect(() => {
    dispatch(setCourses())
  }, []) // [] ensure that this doesn't depend on anything so only runs once. 
  return (
    <div data-testid="Root" className="root">
      <div>
        <Route exact path="/" component={() => <LandingPage />} />
        <Route exact path="/contactus" component={() => <ContactUsPage />} />
        <Route exact path="/faq" component={() => <FaqPage />} />
        <Route
          exact
          path="/course/:courseId"
          render={props => (
            <CourseIntroductionPage courseId={props.match.params.courseId} />
          )}
        />
        <Route
          exact
          path="/course/:courseId/:standard"
          render={props => (
            <SyllabusPage courseId={props.match.params.courseId} standard={props.match.params.standard}/>
          )}
        />
        
      </div>
    </div>
  )
}

export default Root
